.page{
    width: 100%; 
    height: 100%; 
    position: absolute;

}

.top-tags{
    bottom: auto; 
    top: 35px; 

}

.tags{ 
    color: #ffff;
    opacity: 0.6;
    position: absolute;
    bottom:0;
    left: 120px; 
    font-size: 18px; 
    font-family: 'La Belle Aurore';

}

.bottom-tag-html{
    margin-left: -20px;
}

.container{
    width:100%; 
    will-change:contents;
    height: 90%; 
    min-height:566px; 
    position: absolute; 
    opacity: 0; 
    top: 5%; 
    margin: 0 auto; 
    z-index: 1; 
    transform-style: preserve-3d;
    animation: fadeIn 1s forwards;
    animation-delay:0s; 

}

.profile-page{
    .text-zone{
        position: absolute; 
        left: 10%; 
        transform: translateY(-50%);
        width: 35%; 
        max-height: 90%; 
        vertical-align: middle;
        display: table-cell;

        h1{ 
            font-size: 53px; 
            font-family: 'Coolvetica';
            color: white; 
            font-weight: 400; 
            margin-top: 0; 
            position: relative; 
            margin-bottom: 40px; 
            left: 10px; 
        }
        p{
            font-style: 13px;
            color: white;
            font-family:sans-serif;
            font-weight: 300;
            min-width: fit-content;
            

        }
    }
}